<template>
      <v-row style="height:100%;">

                    <v-card height="100%" class="pa-5" max-width="590px">
                      <v-sheet height="5%" class="text-h6"> Reporte General </v-sheet>
                      <v-sheet height="670" class="overflow-y-auto">
                        <generalreport/>
                      </v-sheet>
                      <v-btn height="5%" color="indigo" dark @click="onsubmit">Imprimir</v-btn>
                    </v-card>

              <!--
                <v-col cols="4">
                    <v-card height="100%">
                      <v-card-title>Reporte Semanal</v-card-title>
                    </v-card>
                </v-col>

                <v-col cols="4">
                    <v-card height="100%">
                      <v-card-title>Reporte del dia</v-card-title>
                    </v-card>
                </v-col>
                -->

      </v-row>
</template>
<script>
import html2canvas from 'html2canvas';
import pdfMake from 'pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts.js'
pdfMake.vfs = pdfFonts.pdfMake.vfs;
import generalreport from '@/views/personal/generalreport.vue'

export default {
  name: 'reportes',
  components: {
      generalreport
  },
  created: function () {

  },
  data: () => ({
     options: {
        chart: {
          id: 'frecuencia-uso',
           toolbar: {
              show: false
            }
        },
      },
      series: [{
        name: 'Equipo usado',
        data: [10, 20, 50, 100]
      }],
      style_doc:  {
              header: {
                fontSize: 18,
                bold: true
              },
              subheader: {
                fontSize: 13,
                bold: true
              },
              quote: {
                italics: true
              },
              small: {
                fontSize: 8
              }
      }
  }),
  methods: {
    async onsubmit(){

      console.log(this.$refs)


        var dd = {
            content: [
               {	image: (await this.render("pg_icon")).toDataURL("image/png") , width: 500},
               {
                  text: 'Reporte General',
                  style: 'header'
                },
                {
                  text: 'Centro Nacional de Investigacin y Desarrollo Tecnolgico',
                  style: 'subheader',
                  alignment: 'right'
                },
                {
                  text: 'Sistema para el control de entrada y salida de equipo',
                  alignment: 'right'
                },
                {	image: (await this.render("pg_chart")).toDataURL("image/png") , width: 500}
                
               
                
            ],
            styles: this.style_doc
                  
          }
        pdfMake.createPdf(dd).open();
    },
    render(id){

      return html2canvas(document.querySelector("#"+id), {
            scale: 3,
            imageTimeout: 5000
          })

    }

  }
}
</script>